@import url(//db.onlinewebfonts.com/c/860c3ec7bbc5da3e97233ccecafe512e?family=Circular+Std+Book);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
:root {
    --min-width: 40px;
    --max-width: 150px;
}

#header {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 1rem;
    font-size: 1.5rem;
    font-family: "Circular Std Book", sans-serif;
    justify-content: space-evenly;
}

#header>div {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: clamp(40px, 20%, 150px);
    width: clamp(var(--min-width), 20%, var(--max-width));
    padding-bottom: clamp(40px, 20%, 150px);
    padding-bottom: clamp(var(--min-width), 20%, var(--max-width));
}

#header>div>div {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 70%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    cursor: pointer;
    background-color: #d1ccc3;
    box-shadow: 20px 20px 70px #d4d4d4, -20px -20px 70px #ffffff;
    font-size: 3rem;
    border: 3px solid #bbbbbb;
}
@font-face {
    font-family: "Circular Std Book";
    src: url("//db.onlinewebfonts.com/t/860c3ec7bbc5da3e97233ccecafe512e.eot");
    src: url("//db.onlinewebfonts.com/t/860c3ec7bbc5da3e97233ccecafe512e.eot?#iefix") format("embedded-opentype"),
        url("//db.onlinewebfonts.com/t/860c3ec7bbc5da3e97233ccecafe512e.woff2") format("woff2"),
        url("//db.onlinewebfonts.com/t/860c3ec7bbc5da3e97233ccecafe512e.woff") format("woff"),
        url("//db.onlinewebfonts.com/t/860c3ec7bbc5da3e97233ccecafe512e.ttf") format("truetype"),
        url("//db.onlinewebfonts.com/t/860c3ec7bbc5da3e97233ccecafe512e.svg#Circular Std Book") format("svg");
}

html,
body,
#root,
main {
    width: 100%;
    height: 100%;
    margin: 0;
    color: black;
}

main {
    display: flex;
    flex-direction: column;
    background: linear-gradient(#fafafa, #ffffff);
    font-family: "Circular Std Book", sans-serif;
}

* {
    box-sizing: border-box;
}
.timetable-container {
    position: relative;
    width: 100%;
    flex-grow: 1;
    overflow-x: hidden;
    scroll-behavior: smooth;
}
:root {
    --cell-margin: 3vh;
    --indicator-width: 15px;
}

.timetable-page-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    transition: left .6s cubic-bezier(0.83, 0, 0.17, 1);
}

.cell {
    border-radius: 1rem;
    margin: 0 1rem;
    margin-bottom: 3vh;
    margin-bottom: var(--cell-margin);
    height: 6rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    cursor: pointer;
    transition: height .6s cubic-bezier(0.83, 0, 0.17, 1);
}

.bg {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    border-radius: 1rem;
}

.bg div {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: clamp(13px, calc(13px + 12 * (100vw - 300px) / 600), 25px);
}

.cell:first-child {
    margin-top: 3vh;
    margin-top: var(--cell-margin);
}

.date {
    width: 100%;
    height: 63px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Poppins", sans-serif;
    font-size: clamp(18px, calc(18px + (22) * (100vw - 300px) /1000), 40px);
    color: black !important;
}

[class|=bottom] {
    position: absolute;
    height: 100%;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    z-index: 0;
    border-radius: 1rem;
}

.bottom-0 {
    top: 1vh;
    width: 95%;
    opacity: .5;
}

.bottom-1 {
    top: 2vh;
    width: 85%;
    opacity: .3;
}

.online-indicator {
    position: absolute;
    z-index: 1;
    width: 15px;
    width: var(--indicator-width);
    height: 15px;
    height: var(--indicator-width);
    border-radius: 15px;
    border-radius: var(--indicator-width);
    right: 10px;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
}


@media screen and (min-width: 500px) {

    .bg {
        display: grid;
        grid-template: repeat(3, 1fr) / 1fr 1fr;
    }

    .bg div {
        justify-content: center;
        padding-left: 1rem;
    }

    .bg div:nth-child(1) {
        justify-content: center;
        grid-row: 1 /2;
        grid-column: 1 / 3;
    }

    .bg div:nth-child(2) {
        grid-row: 2 /3;
    }

    .bg div:nth-child(3) {
        grid-row: 2 /3;
    }

    .bg div:nth-child(4) {
        grid-row: 3 /4;
        grid-column: 1 / 3;
    }
}
